<template>
  <gmap-marker
    :position="gPosition"
    :icon="icon"
  ></gmap-marker>
</template>

<script>
import MixinMapMarker from '../../mixins/MapMarker.js';

export default {
  name: 'google-map-marker',
  mixins: [MixinMapMarker],

  computed: {
    gPosition() {
      return {
        lng: this.coordinates[0],
        lat: this.coordinates[1],
      };
    },
  },
};
</script>